import React, { FC } from 'react';
import { useForm } from 'react-hook-form';
import { useSelector } from 'react-redux';
import { RouteComponentProps } from 'react-router';
import { Col, CustomInput, FormGroup, Row } from 'reactstrap';
import { InputGroupInline, InputTooltipProps, InputType, REFERENCE, WindowComponent, WindowComponentProps, withTooltip } from 'summer';
import { hasEmpresa } from '../../../../../dependencies/empresaDependency';
import { hasEmpresaMCG } from '../../../../../dependencies/empresaMCGDependency';
import { hasGrupoEmpresaConfig } from '../../../../../dependencies/grupoEmpresaConfigDependency';
import { getDefaultReportBasicValues } from '../../../../../models/dtos/ger/reportBasicValues';
import { getDefaultReportViewOptions, ReportViewOptions } from '../../../../../models/dtos/ger/reportViewOptions';
import { Reducers } from '../../../../../store/ducks';
import { GlobalState } from '../../../../../store/ducks/global.duck';
import ReportForm from '../../../../components/report/ReportForm';
import { AnaliseFinanceiraRelRV, CLASS_KEY, REPORT_EVENT, REPORT_TITLE } from './analiseFinanceiraRelRV';

const DEFAULT_VALUES: AnaliseFinanceiraRelRV = {
  ...getDefaultReportBasicValues(REPORT_TITLE),
  gerarCabecalhoPadraoAdministradora: true,
};

const DEFAULT_OPTIONS: ReportViewOptions = {
  ...getDefaultReportViewOptions(REPORT_EVENT, REFERENCE.value),
  exibirConfiguracaoDadosEmpresa: true,
  exibirSegmentacaoFolha: true,
};

const AnaliseFinanceiraReportView: FC<WindowComponentProps & RouteComponentProps> = props => {
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);

  const { handleSubmit, ...formProps } = useForm<AnaliseFinanceiraRelRV>({ defaultValues: DEFAULT_VALUES, mode: 'onBlur' });

  const propToolTip = (messagem: string): InputTooltipProps => {
    return {
      message: messagem,
      placement: 'top-start',
    };
  };

  return (
    <WindowComponent
      title="Análise Financeira - Índices de Liquidez"
      useGlobalParameter={true}
      globalParameter={globalReducer.globalParameter}
      dependencies={[
        () => hasEmpresa(globalReducer.globalParameter),
        () => hasEmpresaMCG(globalReducer.globalParameter),
        () => hasGrupoEmpresaConfig(globalReducer.globalParameter),
      ]}
      wikiId={1049}
    >
      <ReportForm
        {...props}
        handleSubmit={handleSubmit}
        formProps={formProps}
        localStorageKey={CLASS_KEY}
        routeProps={props}
        reportBasicValues={DEFAULT_VALUES}
        reportViewOptions={DEFAULT_OPTIONS}
      >
        <FormGroup tag={Row}>
          <Col>
            <CustomInput id="idimprimirData" label="Incluir Assinatura" name="imprimirData" innerRef={formProps.register} type="switch" />
            {withTooltip(propToolTip('Imprime a data completa do lançamento no formato dd/mm/aaaa ao invés de apenas o dia.'), 'imprimirData')}
          </Col>
        </FormGroup>
        {formProps.getValues().imprimirData ? (
          <Row>
            <Col>
              <InputGroupInline
                formProps={{ ...formProps, validation: { required: true } }}
                id="dataAssinatura"
                label="Data da Assinatura"
                placeholder="Informe a Data da Assinatura"
                isPrependLabel={true}
                type={InputType.DATE}
              />
            </Col>
          </Row>
        ) : null}
      </ReportForm>
    </WindowComponent>
  );
};

export default AnaliseFinanceiraReportView;
